body {
  margin: 0;
  overflow: hidden;
  font-family: Arial, sans-serif;
}

.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  cursor: grab;
}

.grid-container {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.grid {
  position: absolute;
  width: 200%;
  height: 200%;
  background: repeating-linear-gradient(
      0deg,
      #ddd,
      #ddd 1px,
      transparent 1px,
      transparent 20px
    ),
    repeating-linear-gradient(
      90deg,
      #ddd,
      #ddd 1px,
      transparent 1px,
      transparent 20px
    );
}

.timeline {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: blue;
}

.blue-line {
  position: absolute;
  height: 100%;
}

.current-time-circle {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: blue;
  border-radius: 50%;
  top: -4px;
  left: 0;
}

.line {
  position: absolute;
  height: 2px;
  background-color: gray;
  width: 100vw;
  left: 10px;
}
